import { useEffect, useState } from "react";
import { byTitle, uniqueBeers } from "../../helpers/inventory";
import CartItem from "./CartItem";
import "./styles.index.css";

const Cart = ({ datas }) => {
  const [beerList, setBeerList] = useState([]);
  const reducer = (accumulator, currentValue) => accumulator + currentValue;

  useEffect(() => {
    const newList = {};
    const zeroList = {};
    const allBeers = uniqueBeers();

    allBeers.forEach(beer => {
      zeroList[beer] = 0;
    });

    datas.forEach(data => {
      const beerByTitle = byTitle(data);
      beerByTitle.forEach(bbt => {
        const beer = bbt["beer"];
        delete zeroList[bbt["beer"]];
        if (!(beer in newList)) {
          newList[beer] = 0;
        }
        newList[bbt["beer"]] += parseInt(bbt["quantity"]);
      });
    });

    const sortedNewList = {};
    Object.keys(newList).sort().forEach(key => {
      sortedNewList[key] = newList[key];
    });

    const sortedZeroList = {};
    Object.keys(zeroList).sort().forEach(key => {
      sortedZeroList[key] = zeroList[key];
    });

    const fullList = {...sortedNewList, ...sortedZeroList};

    setBeerList(fullList);
  }, [datas]);

  return (
    <>
      <h3>Cart</h3>
      <h4>What to Buy</h4>
      <ul>
        {datas.sort().map(value => {
          return <li key={value}>{value}</li>;
        })}
      </ul>

      <hr/>
      <h4>Beer Totals ( {Object.values(beerList).reduce(reducer, 0)} cans )</h4>
      <ul>
        {Object.keys(beerList).map(value => {
          return <li key={value}><CartItem beer={value} quantity={beerList[value]} /></li>;
        })}
      </ul>
    </>
  );
};

export default Cart;
