import { useEffect, useState } from "react";
import { byTitle } from "../../helpers/inventory";
import "./styles.index.css";

const Product = ({ title, cart, setCart, handleRemoveProduct, butts }) => {
  const [count, setCount] = useState(0);
  const beers = byTitle(title);

  useEffect(() => {
    setCount(cart.filter(x => x === title).length);
  }, [title, cart]);

  const handleAdd = () => {
    setCart([...cart, title]);
  }

  const handleRemove = () => {
    if (count > 0) {
      const newCart = cart.filter(item  => item !== title);
      setCart(newCart);
    }
  }

  const inCart = beer => {
    const isInCart = cart.some(title => {
      return byTitle(title).some(b => b["beer"] === beer);
    });
    if (isInCart) {
      return "beer-in-cart";
    }
    return "";
  };

  return (
    <div className="Product">
      <h3>{title} {butts && <button value={title} onClick={handleRemoveProduct}>Remove</button>}</h3>
      <div className="wrapper">
        { beers.map(beer => {
          return <><span className="beer-quantity">{`${beer["quantity"]}`}x </span><span className={`beer-name ${inCart(beer["beer"])}`}>{`${beer["beer"]}`}</span><br/></>;
        }) }
      </div>
      <div>
        <button onClick={handleAdd}>Add</button>
        <button onClick={handleRemove}>Remove</button>
        <span className="case-amount">{count}</span>
      </div>
    </div>
  );
};

export default Product;
