import { useEffect, useState } from "react";
import { DATAS, byType, uniqueTitles } from "./helpers/inventory";
import Cart from "./components/Cart";
import Product from "./components/Product";
import Shopper from "./components/Shopper";
import './App.css';

function App() {
  const [cart, setCart] = useState([]);
  const [uniques, setUniques] = useState([]);
  const [masterList, setMasterList] = useState(DATAS);

  useEffect(() => {
    const t = uniqueTitles({type: "mixed"}, masterList);
    setUniques(t);
  }, [masterList]);

  const handleRemoveProduct = e => {
    const input = e.target.value;
    const newMasterList = masterList.filter(item => item["title"] !== input);
    setMasterList(newMasterList);
  };

  return (
    <div className="AppContainer">
      <div className="row">
        <div className="column">
          <Shopper cart={cart} setCart={setCart} masterList={masterList} />
        </div>
      </div>
      <div className="row">
        <div className="App column">
          {uniques.map(value => {
            return (<><Product title={value} cart={cart} setCart={setCart} key={value} handleRemoveProduct={handleRemoveProduct} butts={true} /></>);
          })}
          <hr/>
          <h3>Cases</h3>
          {byType("case").map(value => {
            return (<><Product key={value["title"]} title={value["title"]} cart={cart} setCart={setCart} /></>);
          })}
          <hr/>
          <h3>4 Packs</h3>
          {byType("four").map(value => {
            return (<><Product key={value["title"]} title={value["title"]} cart={cart} setCart={setCart} /></>);
          })}
          <hr/>
          <h3>6 Packs</h3>
          {byType("six").map(value => {
            return (<><Product key={value["title"]} title={value["title"]} cart={cart} setCart={setCart} /></>);
          })}
          <hr/>
          <h3>12 Packs</h3>
          {byType("twelve").map(value => {
            return (<><Product key={value["title"]} title={value["title"]} cart={cart} setCart={setCart} /></>);
          })}
          <hr/>
          <h3>Singles</h3>
          {byType("single").map(value => {
            return (<><Product key={value["title"]} title={value["title"]} cart={cart} setCart={setCart} /></>);
          })}
        </div>
        <div className="column">
          <div className="Cart">
            <Cart key={"cart"} datas={cart} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
