import { useEffect, useState } from "react";
import { goShoppin, uniqueBeers } from "../../helpers/inventory";
import "./styles.index.css";


const Shopper = ({ cart, setCart, masterList }) => {
  const [beer, setBeer] = useState("");
  const [quantity, setQuantity] = useState("");
  const [choices, setChoices] = useState([]);
  const [shoppingList, setShoppingList] = useState([]);
  const [beers, setBeers] = useState([]);


  useEffect(() => {
    setBeers(uniqueBeers(masterList));
  }, [masterList]);

  const handleChange = e => {
    const input = e.target.value;
    setBeer(input);
    if (!input) {
      return setChoices([]);
    }
    const options = beers.filter(beer => beer.toLowerCase().match(new RegExp(`^${input.toLowerCase()}.*`)));
    setChoices(options);
  };

  const handleQuantityChange = e => {
    const input = parseInt(e.target.value);
    setQuantity(input);
  };

  const handleAdd = () => {
    shoppingList.push({beer, quantity});
    setShoppingList([
      ...shoppingList,
    ]);
    const newCart = goShoppin(cart, shoppingList, masterList);
    setCart(newCart);
    setBeer("");
    setQuantity("");
  };

  const handleClick = e => {
    setBeer(e.target.textContent);
    return setChoices([]);
  };

  const handleRemove = e => {
    const input = e.target.value;
    const newList = shoppingList.filter(item => item["beer"] !== input);
    setShoppingList(newList);
  };

  return (
    <>
      <div className="">
      </div>

      <div className="shopper-wrapper open">
        <input onChange={handleChange} value={beer} /> x<input value={quantity} onChange={handleQuantityChange} /> <button onClick={handleAdd}>Add</button> Add beer and amount here to auto calculate cases needed to hit that beer amount.
        <div className="autocomplete-items">
          {
            choices.map(choice => {
              return <div key={choice} onClick={handleClick}>{choice}</div>
            })
          }
        </div>
        <div>
          {
            shoppingList.map(item => {
              return <p>{item["beer"]} x{item["quantity"]}<button onClick={handleRemove} value={item["beer"]}>Remove</button></p>
            })
          }
        </div>
      </div>
    </>
  );
};

export default Shopper;
